.body.responsive {
  overflow: hidden;
}

.nav {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  font-family: 'Montserrat', sans-serif;
}

.nav-bar {
  display: flex;
  align-self: center;
}

.nav-bar__item {
  margin-left: 31px;
  font-weight: 600;
  font-size: 12px;
  line-height: 24px;
  color: black;
}

.nav-bar__item:first-child {
  display: none;
}

.nav-bar__item:hover, .nav__logo:hover {
  opacity: .7;
  transition: .2s;
}

@media (max-width: 768px) {
  .nav-bar__ {
    display: none;
  }
  .nav-bar__:last-child {
    display: block;
  }
}

@media (max-width: 767px) {
  .hamburger-button {
    display: block;
  }
  .nav-bar {
    margin-left: auto;
    margin-right: 0;
  }
  .nav-bar__item:not(:first-child) {
    display: none;
  }
  .nav-bar__item:first-child {
    display: block;
  }
  .nav-bar.responsive {
    position: relative;
    left: 0;
    bottom: 0;
    right: 0;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;
    transition: all .3s linear;
  }
  .nav-bar.responsive .nav-bar__item {
    margin: 20px auto;
    color: black;
    display: block;
    font-size: 22px;
    transition: opacity .3s linear;
  }
  .nav-bar.responsive .nav-bar__item > a, span::before {
    color: black;
  }
  .menu-button--active {
    margin: 0 auto;
    margin-top: 30px;
  }
}
